
canvas{
			position: absolute;
			top: 0;
			left: 0;
			z-index: 0;
			width: 100%;
			height: 100%;
			pointer-events: none;
}
#wrap{
			font-family: "微软雅黑";
			position: absolute;
			left: 0;
			width: 320px;
			text-align: center;
			top: 50%;
			left: 50%;
			margin-left: -160px;
			margin-top: -160px;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			    user-select: none;
			z-index: 1;
}
a{
			text-decoration:none;
			-webkit-transition:.3s;
			transition:.3s;
			color:#999;
}
a:hover{
			color:red;
}



